export default {
    //核心框架的版本
    version: "2.0.2",

    webRoot: {
        //此项目的文件夹名称，平台的配置，项目组请勿修改
        idm:"idm",
        //框架的所属项目的根目录名称，
        default: "/DreamWeb/",
        //页面的前置路径，可相对或绝对（./或../idm/或/DreamWeb/idm/）
        htmlDir: "./",
        //打包编译的静态资源文件，相对于当前的页面路径
        assetsDir: "./static/",
        /**
         * 组件相对此框架所在项目的目录(以idm_modules放在程序根目录为举例)
         * 框架页面index.html在DreamWeb/idm/A  下面，则为../../idm_modules/或/DreamWeb/idm_modules/
         * 框架页面index.html在DreamWeb/ 下面，则为./idm_modules/或/DreamWeb/idm_modules/
         */
        moduleDir: "./idm_modules/",
        /**
         * 平台的编号，此项相当于一个产品的基本编号，所有编译后的程序应该放到这个编号下面
         * DreamWeb的默认为p1000,项目组请勿修改，否则造成程序无法访问
         * 如无编号可留空
         */
        platformNo:"p1000",
        /**
         * 项目的编号，此项相当于一个项目的扩展编号，所有上传的文件都会和配置的文件都会保存在此编号的文件夹下面
         * 如果与platformNo为相同编号则默认为都是平台的
         * 如无编号可留空
         */
        projectNo:"p1000",
        /**
         * 组件的存放路径，相对于程序根目录的绝对的路径
         */
        moduleAbsoluteDir:"/{projectNo}/idm/idm_modules/"
    },
    //页面结构（页面设置数据）
    pageSchema: {
        //保存方式，支持磁盘存储JSON格式（disk）和数据库存储（database）
        saveType: "database",
        //磁盘存储json格式的相对路径地址（支持{version}和{pageid}替换）
        jsonDir: "/{projectNo}/idm/idm_pageschema/{pageid}/{version}.json"
    },
    // api地址，以下属性为空则会使用mockdata对应下面的数据源
    api: {
        /**
         * 组件市场接口地址，带分组结构的（/ctrl/idm/api/fetchComponentMarketData）
         * 无参数
         */
        componentMarketUrl: "",
        /**
         * 页面的版本接口地址（/ctrl/idm/api/fetchPageVersionList)
         * 接收以下参数：
         * pageid：页面ID
         */
        pageVersionListUrl: "/ctrl/idm/api/fetchPageVersionList",
        /**
         * 页面的配置数据地址（/ctrl/idm/api/fetchPageSettingData)
         * 接收以下参数：
         * pageid：页面ID
         * version：页面版本号（如果版本号为空则需要返回最新的版本号的数据，不管saveType为disk还是database都是如此）
         * savetype:当前配置的存储方式==》磁盘存储JSON格式（disk）和数据库存储（database）
         */
        pageSettingDataUrl: "/ctrl/idm/api/fetchPageSettingData",
        /**
         * 自定义业务（布局）组件的配置数据地址（/ctrl/idm/api/fetchCustomModuleSettingData)
         * 接收以下参数：
         * pageid：页面ID
         * schemaType：0：布局组件，1：业务组件
         */
         customModuleSettingDataUrl: "/ctrl/idm/api/fetchCustomModuleSettingData",
        /**
         * 保存页面配置接口地址(/ctrl/idm/api/savePageSchema)
         * 接收以下参数：
         * pageid：页面ID
         * pageSettingData：页面结构数据json字符串，保存到数据库或json文件即可
         * version：版本号
         * author：作者
         * remark：备注
         * isUse：是否将此版本设为使用版本，1代表使用，0代表不使用
         * traceList：历史记录操作数据,格式：[{actionType:动作类型、pageid：页面ID、pageVersion：页面版本、opDate：操作日期、opTime：操作时间、opContent：操作记录内容对象}]
         * projectNo：项目编号
         */
        savePageSettingUrl: "/ctrl/idm/api/savePageSchema",
        /**
         * 保存自定义组件配置接口地址(/ctrl/idm/api/saveCustomModule)
         * 接收以下参数：
         * pageid：组件ID
         * title：页面标题
         * previewImgJson：预览的图片JSON
         * moduleType：组件类型：0：布局组件，1：业务组件
         * moduleIconfont：组件图标
         * moduleContainerCount：布局组件的容器数量
         * pageSettingData：组件结构数据json字符串，保存到数据库即可
         * author：作者
         * remark：备注
         * traceList：历史记录操作数据,格式：[{actionType:动作类型、pageid：页面ID、pageVersion：页面版本、opDate：操作日期、opTime：操作时间、opContent：操作记录内容对象}]
         * projectNo：项目编号
         */
        saveCustomModuleUrl:"/ctrl/idm/api/saveCustomModule",
        /**
         * 上传图片、文件的接口地址（主要用于框架、公共上传方法）
         * 接收以下参数：
         * type：dev_uploadimage_ctrl
         * file：文件（java使用【((MultipartHttpServletRequest)HttpServletRequest).getFile("file")】获取）
         * ...IDM.setting.webRoot
         * 返回结果示例：
         * {
         *  fileName: "tab设置.png"
         *  filePath: "/upload/idmfiles/f22081da-9410-40bc-afa0-6b3106c45c1c.png"
         *  fileSize: "218KB"
         * }
         */
        uploadFileServerUrl: "/ctrl/idm/api/fileupload",
        /**
         * 保存裁剪后的图片base64转存为图片文件
         * 接收以下参数：
         * type：dev_uploadimage_ctrl
         * data：图片base64
         * suffix:后缀名 jpeg, png, webp
         * ...IDM.setting.webRoot
         * 返回结果示例：
         * {
         *  fileName: "tab设置.png"
         *  filePath: "/upload/idmfiles/f22081da-9410-40bc-afa0-6b3106c45c1c.png"
         *  fileSize: "218KB"
         * }
         */
        saveCropperBase64ImageUrl:"/ctrl/idm/api/saveBase64ToImageFile",
        /**
         * 开发环境的获取操作痕迹的列表记录
         * 接收以下参数：
         * pageid：页面ID
         * startIndex：开始索引
         * pageNumber：每页显示大小
         * 返回结果示例：
         * [{
         *  id:数据ID，没有实际意义，只有有删除功能才有意义
         *  actionType: 动作类型数字
         *  pageId:页面ID
         *  pageVersion:页面版本
         *  opDate:操作日期
         *  opTime:操作时间
         *  opContent:操作实际内容
         * }]
         */
        developTraceListUrl:"/ctrl/idm/api/fetchDevelopTraceListInfo",
        /**
         * 获取应用信息的接口地址
         * 接收以下参数：
         * urlData:url地址中的全部参数地址,
         * pageid：页面ID
         * 返回结果内容不固定
         */
        applicationInfoUrl:"/ctrl/idm/api/fetchApplicationInfo",
        /**
         * 获取当前登录用户信息的接口地址
         * 接收以下参数：
         * urlData:url地址中的全部参数地址,
         * pageid：页面ID
         * 返回结果内容不固定
         */
        userInfoUrl:"/ctrl/idm/api/fetchUserInfo",
        /**
         * 页面个性化保存的接口地址
         * 接收以下参数：
         * urlData:url地址中的全部参数地址,
         * pageid：页面ID
         * version：页面版本号
         * customData：个性化定制的数据，结构为对象，对象下包含数组层级的
         * 
         * 返回结果示例(使用统一结构，data返回为null即可)：
         */
        saveUserCustomizationUrl:"/ctrl/idm/api/saveUserCustomization",
         /**
          * 页面个性化获取的接口地址
          * 接收以下参数：
          * urlData:url地址中的全部参数地址,
          * pageid：页面ID
          * version：页面版本号，如果没有传则会直接取用户最新设置的一条
          * 
          * 返回结果示例：
          * {
          *    "code":"200",
          *    "message":"",
          *    "data":{
          *      id:"",
          *      idmId:"",
          *      pageId:"",
          *      pageVersion:"",
          *      customData:"此处为用户个性化定制后的数据，为保存的时候传的customData,如果发现此处data为null则使用/ctrl/idm/api/fetchPageSettingData接口",
          *      userId:"",
          *    }
          * }
          */
        userCustomizationUrl:"/ctrl/idm/api/fetchUserCustomization",
        /**
         * 表单提交元数据的URl地址，用户可以自定义
         * 接收以下参数：
         * urlData:url地址中的全部参数地址,
         * pageId：页面ID,
         * moduleData:表单的所有数据
          * 返回结果示例：
          * {
          *    
          * }
         */
        saveFormsMetaDataUrl:"/ctrl/idm/api/saveFormsMetaData",
        /**
         * 文档管理的代码包数据url地址（/ctrl/idm/console/fetchAllCodePackageList）
         * 返回结果示例：
         * {
         * }
         */
        allCodepackageUrl:"/ctrl/idm/console/fetchAllCodePackageList",
        /**
         * 获取组件控制中心（动态属性）设置的数据的url地址（/ctrl/idm/console/fetchMarketModuleAttrUserSetData）
         * 返回结果示例：
         * [{
         * id:"",
         * attrCode:"",
         * attrData:"",
         * ...
         * }]
         */
        dynamicAttributeListUrl:"/ctrl/idm/console/fetchMarketModuleAttrUserSetData",
    },
    /**
     * mockdata地址，只有对应的API地址为空的时候才会使用这里的数据
     * 前缀$代表：使用webRoot > assetsDir替换
     * 前缀~代表：使用webRoot > htmlDir替换
     * 前缀@代表：使用webRoot > moduleDir替换
     * 前缀./或../或http://或https://则不替换，原模原样使用地址
     * 其他情况使用webRoot > default替换
     */
    mockurl: {
        /**
         * 组件市场测试数据结构
         */
        componentMarketUrl: "$/mockdata/ComponentMarketData.json",
        /**
         * 页面的版本测试数据结构
         */
        pageVersionListUrl: "$/mockdata/PageVersionListData.json",
        /**
         * 页面的配置数据的数据结构
         */
        pageSettingDataUrl: "$/mockdata/pageSettingDataData.json",
        /**
         * 自定义业务（布局）组件的配置数据结构
         */
         customModuleSettingDataUrl: "$/mockdata/customModuleSettingDataData.json",
         /**
          * 文档管理的代码包数据结构
          */
         allCodepackageUrl:"$/mockdata/AllCodePackageListData.json",
    },
    //页面配置工具
    develop: {
        /**
         * 开发工具菜单栏中的IDM学院地址，可以是相对路径，也可以是绝对路径
         * 前缀$代表：使用webRoot > assetsDir替换
         * 前缀~代表：使用webRoot > htmlDir替换
         * 前缀@代表：使用webRoot > moduleDir替换
         * 前缀./或../或http://或https://则不替换，原模原样使用地址
         * 其他情况使用webRoot > default替换
         */
        schoolsUrl: "http://school.yunit.cn",
        /**
         * 开发工具菜单栏中的组件开发地址，前缀同理上面
         */
        devComponentUrl: "https://yunit-code.github.io/zh/moduledevelop/introduce.html",
        /**
         * 开发工具菜单栏中的帮助文档地址，前缀同理上面
         */
        helpDocumentUrl: "https://yunit-code.github.io/zh/",
        /**
         * 开发工具菜单栏中文档管理地址，前缀同理上面
         */
         documentManageUrl: "./index.html#/document",
        /**
         * 拖拽区域的大小列表配置
         * {
                key: 从3开始，保证不重复即可,
                width: 拖拽区域的实际宽度（px）,
                height: 拖拽区域的实际高度（px）,
                showText: 要显示的文本,
                type: 类型值
            }
         * type:-1 线条
         * type:3  系统内置的尺寸
         */
        dragAreaSizeList: [
            {
                key: '3',
                width: 1366,
                height: 768,
                showText: '1366*768',
                type: 3
            },
            {
                key: '4',
                width: 1920,
                height: 1080,
                showText: '1920*1080',
                type: 3
            },
            {
                key: '5',
                width: 375,
                height: 667,
                showText: 'iphone6/7/8',
                type: 3
            },
            {
                key: '6',
                width: 414,
                height: 736,
                showText: 'iphone6/7/8 Plus',
                type: 3
            },
            {
                key: '7',
                width: 768,
                height: 1024,
                showText: 'iPad(768*1024)',
                type: 3
            },
            {
                key: '8',
                width: 1024,
                height: 768,
                showText: 'iPad(1024*768)',
                type: 3
            }
        ],
        /**
         * 拖拽区域大小默认值key，url中传入dragSizeKey也会有效
         * 默认为1：代表适应当前尺寸
         */
        dragAreaSizeDefaultKey:"1",
        /**
         * 组件市场的列表默认显示类型
         * grid：网格类型
         * list：列表类型
         */
        componentMarketListDefaultType:"grid",
        /**
         * 开启开发工具中获取用户信息，注意：只会使用配置项中（userInfoUrl）的接口进行加载，页面属性设置中的无法进行加载。
         */
        openFetchUserInfo:true,
        /**
         * 开启开发工具中工作区的放大缩小功能
         */
        scaleOpen:true,
        /**
         * 配置开发工具中工作区的放大缩小比例列表
         */
        scaleList:[25,50,75,100,125,150,200,300,400,600,800,1000],
        /**
         * 配置开发工具中组件属性控件（页面选择：pageSelect）页面类型的检索条件
         * 如果为数组格式则是属于静态数据，格式：[{text:"PC",value:"0"},...]
         * 为字符串格式则视为通过接口获取，返回格式为：
         * 返回结果示例：
         * {
         *    "code":"200",
         *    "message":"",
         *    "data":[{text:"PC",value:"0"},...]
         * }
         */
        pageSelectConditionType:[{text:"PC",value:"0"},{text:"移动端",value:"1"},{text:"响应式",value:"2"}],
        /**
         * 配置开发工具中组件属性控件（页面选择：pageSelect）页面排序的检索条件
         * 如果为数组格式则是属于静态数据，格式：[{text:"创建时间正序",value:"cjsjzx"},...]
         * 为字符串格式则视为通过接口获取，返回格式为：
         * 返回结果示例：
         * {
         *    "code":"200",
         *    "message":"",
         *    "data":[{text:"创建时间正序",value:"cjsjzx"},...]
         * }
         */
        pageSelectConditionSort:[{text:"创建时间正序",value:"cjsjzx"},{text:"创建时间倒序",value:"cjsjdx"},{text:"优先级排序",value:"yxjpx"}],
        /**
         * 配置开发工具中组件属性控件（页面选择：pageSelect）页面分组的检索条件
         * 如果为数组格式则是属于静态数据，格式：[{text:"首页",value:"index"},...]
         * 为字符串格式则视为通过接口获取，返回格式为：
         * 返回结果示例：
         * {
         *    "code":"200",
         *    "message":"",
         *    "data":[{text:"首页",value:"index"},...]
         * }
         */
        pageSelectConditionGroup:"/ctrl/idm/console/fetchYMFZOptionList",
        /**
         * 页面列表查询Api地址
         * 请求参数：
         * {
         * pageIndex:页码
         * pageSize:页大小
         * searchText:文本框检索条件
         * pagetype:页面类型检索条件
         * group:页面分组检索条件
         * sort:页面排序检索条件
         * }
         * 返回结果：
         * {
         *    "code":"200",
         *    "message":"",
         *    "data":[
         *        {
         *           id: "idm_control_setting_panel_001",
         *           title: "IDM控制中心-组件设置界面",
         *           moduleGroupId: "idmkztpage",
         *           moduleGroupText: "IDM控制台页面",
         *           pageType: "0",
         *           pageRemark: "IDM控制中心-组件设置界面，主要用于所有组件的设置渲染界面，用于普通用户能定制每个组件的效果",
         *           previewImgJson: "[{\"uid\":\"1642733481374xiV1BZAGIMlDEfym\",\"ourl\":\"/p1000/idm/upload/idmfiles/6aaf99da-2053-4684-b80a-642328750d36.jpg\",\"size\":\"90KB\",\"src\":\"/p1000/idm/upload/idmfiles/6aaf99da-2053-4684-b80a-642328750d36.jpg\",\"name\":\"Dingtalk_20220121105100.jpg\",\"width\":1920,\"url\":\"/DreamWeb/p1000/idm/upload/idmfiles/6aaf99da-2053-4684-b80a-642328750d36.jpg\",\"status\":\"done\",\"height\":937}]"
         *        },
         *        ...
         *     ]
         * }
         */
        pageSelectFetchPageListApi:"/ctrl/idm/console/fetchIdmListPageData"
    },
    //文档管理工具配置
    document:{
        /**
         * 代码包的文档相对路径
         * 比如，代码包地址为：idm/idm_modules/basic/1.0.0/
         * 那么，代码包的文档应该在这个代码包地址里面，例如：static/doc/
         * 文档文件名称固定为index.md
         */
        codepackageDocumentPath:"static/doc/",
        /**
         * 代码包内的组件的文档相对路径
         * 比如，代码包地址为：idm/idm_modules/basic/1.0.0/
         * 那么，代码包的文档应该在这个代码包地址里面，例如：static/doc/components/
         * 文档文件名称固定为组件的类名.md，例如按钮的文档名称为：IButton.md
         */
        moduleDocumentPath:"static/doc/components/",
        /**
         * 根据前端的配置保存md文件到对应的目录中
         * 接收以下参数：
         * mdPath:md对应要生成的文件地址
         * type:code代表未代码包的路径，module代表组件的文档路径
         * moduleClassName:组件的类名
         * mdContent:md文档的内容
         */
        saveMarkDownFileUrl:"/ctrl/idm/api/saveDocumentMdFile"
    },
    //应用程序综合配置
    applications:{
        /**
         * 作用于如果标题为空的时候会显示此处默认标题
         */
        defaultTitle:"Idm preview default title",
        /**
         * 页面设置统一的默认登录页地址
         */
        defaultLoginPageUrl:"/ctrl/login",
        /**
         * 主题class名称的前缀
         */
        themeNamePrefix:"idm-theme-",
        /**
         * 组件控制中心的默认设置url
         */
        moduleControlCenterSettingUrl:"~/index.html#/preview/idm_control_setting_panel_001",
        /**
         * 应用程序是否启用项目编号模式的配置，也就是平台代码与组件代码分离的方式，默认为true，中途修改转换可能会造成程序读取异常，请在初始化的时候配置好
         */
        useProjectNoSetting:true,
        /**
         * 组件属性的存储所在的文件夹名称
         */
        moduleAttrFolderName:"attributes",
        /**
         * 组件属性中的字体列表
         */
        fontFamilyList:[
            {
                value:"arial, helvetica, 'microsoft yahei'",
                label:"Arial, Helvetica, 微软雅黑"
            },
            {
                value:"arial, helvetica, simhei",
                label:"Arial, Helvetica, 黑体"
            },
            {
                value:"'comic sans ms', 'microsoft yahei'",
                label:"Comic Sans MS, 微软雅黑"
            },
            {
                value:"'comic sans ms', simhei",
                label:"Comic Sans MS, 黑体"
            },
            {
                value:"impact, 'microsoft yahei'",
                label:"Impact, 微软雅黑"
            },
            {
                value:"impact, simhei",
                label:"Impact, 黑体"
            },
            {
                value:"'lucida sans unicode', 'microsoft yahei'",
                label:"Lucida Sans Unicode, 微软雅黑"
            },
            {
                value:"'lucida sans unicode', simhei",
                label:"Lucida Sans Unicode, 黑体"
            },
            {
                value: "AlibabaPuHuiTi-Regular",
                label: "AlibabaPuHuiTi-Regular"
            },
            {
                value: "PingFangSC-Regular",
                label: "PingFangSC-Regular"
            }
        ],
        /**
         * 默认引用的资源
         */
        commonRequireJsCss:[
            "/resource/js/public.js",
            // "/resource/js/dreamweb-core.js",
            "/websocket/webscoket.js"
        ],
        /**
         * 打开核心框架的资源加载动画
         */
        openIdmCoreLoadStyle:true,
        /**
         * 加载动画的颜色
         */
        idmCoreLoadColor:"#1890ff",
        /**
         * 渲染引擎默认加载颜色
         */
        defaultPreviewLoadColor:"#1890ff",
        /**
         * 页面配置中的微信SDK默认开启关闭状态。
         */
        defaultOpenWeixinJSSDK:true,
        /**
         * 页面配置中的下拉刷新默认开启关闭状态。
         */
         defaultOpenPulltorefresh:true,
         /**
          * http请求统一配置头部列表
          * 配置方式：
          * {
          *    name:"headerName",
          *    type:"constant：常量、cache：从缓存中读取(优先cookie、localStorage、sessionStorage)、cookie：从cookie读取、localStorage：从localStorage中读取、url:从url参数中读取、express：IDM的表达式，可以获取应用信息、用户信息、主题信息、IDM对象等等",
          *    express:"根据上面的类型填缓存KEY、表达式"
          * }
          */
         httpInCommonHeaderList:[]
    },
    //websocket消息配置
    websocket:{
        /**
         * 用于设置调用初始化websocket方法名，websocket脚本可通过commonRequireJsCss引入，也可通过页面扩展开发。
         */
        websocketInitEventName:"initDreamSocketClient",
        /**
         * websocket打开后会调用IDM内置的打开方法，此处用于设置IDM内置的websocket打开后的处理方法的默认值（可在页面设置中修改）
         */
        websocketOpenEventName:"onWebSocketOpen",
        /**
         * websocket收到消息后会调用IDM内置的监听消息方法，此处用于设置IDM内置的websocket收到消息后的处理方法的默认值（可在页面设置中修改），收到消息后会给所有组件发送指定协议（websocket）的值
         */
        websocketMessageEventName:"onWebSocketMessage"
    },
    //微信功能配置
    weixin:{
        /**
         * 要引入的微信js-sdk
         */
        requireJsCss:[
            "~lib/jweixin-1.2.0.js"
        ],
        /**
         * 必填，需要使用的JS接口列表，所有JS接口列表见官方文档（https://developer.work.weixin.qq.com/document/path/90513）
         * 默认：'launch3rdApp', 'request3rdApp', 'onHistoryBack', 'hideAllNonBaseMenuItem', 'hideOptionMenu', 'openUrl', 'closeWindow'
         */
        jsApiList: ['launch3rdApp', 'request3rdApp', 'onHistoryBack', 'hideAllNonBaseMenuItem', 'hideOptionMenu', 'openUrl', 'closeWindow'],
        /**
         * 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
         */
        debug:false,
        /**
         * 签名获取接口地址
         * 默认：/tencentCustomize/getConfig，
         * 返回结果：
         * {
         *   code:"200",
         *   message:"",
         *   type:"success",
         *   data:{
         *      appId:"",
         *      timestamp:"",
         *      nonceStr:"",
         *      signature:"",
         *   }
         * }
         */
        signatureUrl:"/ctrl/tencentCustomize/getConfig"
    },
    /**
     * 会话保持功能
     * 此处专门为应用程序登录信息为session准备的（在需要登录信息永久保持的情况下才建议开启此功能）
     * 处理流程：
     * 1、用户密码登录或单点登录的时候同时后端通过jwt（用户信息+密钥）生成登录令牌并存储在前端（url参数、cookie、localStorage）
     * 2、IDM在请求获取用户信息接口后发现session掉了则会通过后端提供通过jwt重新进行登录的接口去登录，一旦发现登录成功则会二次去请求获取用户信息接口
     * 3、如果jwt登录也失效了则会执行原来的跳转登录地址
     */
    sessionKeep:{
        /**
         * 前端缓存后端生成的密钥名称
         * 1、localStorage
         * 2、cookie
         * 3、url参数
         */
        cacheKeyName:"idm_sessionkeep_token",
        /**
         * 页面配置中的会话保持默认开启状态，true：默认开启，false：默认关闭
         */
        defaultOpenSessionKeep:false,
        /**
         * 重新登录地址
         * 默认登录地址：/silentRelogin
         * 请求方式：post
         * 请求参数：
         * [cacheKeyName]:当前配置的前端缓存的key名称
         */
        defaultSilentReLoginUrl:"/silentRelogin"
    }
}